@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
html,
#root {
  height: 100vh;
} /* style={{ lineHeight: '2' }} */
#intro {
  font-family: "Bangers", cursive !important;
  height: 50vh;
  line-height: 1.5 !important;
  letter-spacing: 0.5rem !important;
  /* font-weight: lighter !important; */
  font-size: 3.5rem !important;
  text-transform: uppercase;
}
#intro span,
#intro h3 {
  padding: 5px 10px;
  color: rgb(135, 132, 187) !important;
  background-color: black;
}
.video {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  opacity: 0.85;
}
.btn-lets-talk {
  text-transform: uppercase !important;
  border-radius: 0 !important;
  color: white !important;
  background-color: rgb(233, 85, 85) !important;
  -webkit-transition: background-color 1s !important;
  transition: background-color 1s !important;
}
.btn-lets-talk:hover {
  color: rgb(252, 225, 129) !important;
  text-decoration: line-through !important;
  font-weight: bold !important;
  background-color: rgb(114, 38, 38) !important;
}
.social-icon:hover {
  color: rgb(72, 92, 1) !important;
}
.fade-in {
  animation: fadeIn ease 2s;
  -webkit-animation: fadeIn ease 2s;
  -moz-animation: fadeIn ease 2s;
  -o-animation: fadeIn ease 2s;
  -ms-animation: fadeIn ease 2s;
}

@media (max-width: 992px) {
  #intro {
    line-height: 2 !important;
    font-size: 1.5em !important;
  }
  .video {
    object-fit: cover !important;
    height: 100% !important;
    width: 100% !important;
  }
}
@media (max-width: 330px) {
  #intro {
    line-height: 2.5 !important;
    font-size: 0.8rem !important;
  }
  .video {
    object-fit: cover !important;
    height: 100% !important;
    width: 100% !important;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 100%;
  padding: 6.5px;
  margin-right: 17px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* NOTE: slider is inside the switch  */
.slider {
  border: 1.5px solid black;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url(/static/media/day.fd5baf36.png);
  background-color: #f1f1f1;
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 30px;
  width: 30px;
  bottom: 2px;
  left: 2px;
  border: 1.5px solid #ff6b6b;
  background-color: #ff5959c9;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-transition: 0.63s;
  transition: 0.63s;
}

input:checked + .slider {
  background: url(/static/media/night.afeb70a1.png);
  background-color: #003030;
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
}

input:focus + .slider {
  box-shadow: 0 0 15px #003030;
}

input:checked + .slider:before {
  -webkit-transform: translateX(33px);
  transform: translateX(33px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

#titleSkills {
  margin-bottom: 10px;
}

